

/* ------------------------------------------------

		1. Default stylesheets

------------------------------------------------ */

	html{
		overflow-x: hidden;
	}

	.menu_opened{
		left: -150px;
	}

	::-moz-selection{
		background: #cdcdcd;
	}

	::selection{
		background: #cdcdcd;
	}

	::-moz-selection{
		background: #cdcdcd;
	}

	::-webkit-scrollbar{
		background: #cdcdcd;	
		width: 10px;
	}

	::-webkit-scrollbar-thumb{
		background: #181818;
	}

	::-webkit-input-placeholder{
		color:#505050;

		-webkit-transition: text-indent .5s ease, color .5s ease;
				-o-transition: text-indent .5s ease, color .5s ease;
				transition: text-indent .5s ease, color .5s ease;
	}

	input::-moz-placeholder{
		color:#505050;
		opacity:1;
	}

	textarea::-moz-placeholder{
		color: #505050;
		opacity: 1;
	}

	::ms-input-placeholder{
		color:#505050;
	}

	[placeholder]:focus::-webkit-input-placeholder{
		text-indent:10em;
		color:transparent;
	}

	a:focus, a:hover {
		color: #9E258E;
	}
	a {
		color: #6B256E;
	}
	

	

	

