	/* --------------------------------------------
			3.1 Page actions
	-------------------------------------------- */

		#top_actions_wrap{ive
			position: absolute;
			top: 29px;
			right: 30px;
			color: #fff;
		}

		#top_actions_wrap::after{
			content: "";
			display: block;
			clear: both;
		}

		#top_actions_wrap > *{
			display: inline-block;
			vertical-align: middle;
			margin-left: 12px;
		}

		#top_actions_wrap > .action_btn,
		#top_actions_wrap > .action_btn ~ #toggle_menu,
		#top_actions_wrap > .action_btn ~ .nav_searchform,
		#top_actions_wrap > .action_btn ~ .shopping_cart_wrap{
			position: relative;
			top: -12px;
		}

		#top_actions_wrap > .action_btn,
		#top_actions_wrap > .action_btn ~ .nav_searchform,
		#top_actions_wrap > .action_btn ~ .shopping_cart_wrap{
			margin-left: 0;
		}

		#toggle_menu{
			font-size: 30px;
			position: relative;
			z-index: 102;
		}

		#filter_wrap{
			position: relative;
		}

		#filter > li{
			cursor: pointer;

			-webkit-transition: color .7s ease;
					-o-transition: color .7s ease;
					transition: color .7s ease;
		}

		#filter > li:not(:last-child){
			margin-bottom: 6px;
		}

		#filter > li:hover,
		#filter > li.active{
			color: #0097ad;

			-webkit-transition: color .1s ease;
					-o-transition: color .1s ease;
					transition: color .1s ease;
		}

	/* --------------------------------------------
			3.2 Main navigation
	-------------------------------------------- */

		.navigation li{
			position: relative;
		}

		.navigation li a{
			color: #cdcdcd;
			display: block;
		}

		.navigation a:hover,
		.navigation a:focus{
			text-decoration: none;
		}

		.navigation > li > a{
			text-transform: uppercase;
		}

		.navigation .has_submenu > a{
			position: relative;
			padding: 0 20px 0 0;
		}

		.navigation .has_submenu > a::after{
			font-family: 'Fontello';
			content: "\e878";
			font-size: 17px;
			line-height: 0px;
			height: 2px;
			color: #505050;
			display: block;
			position: absolute;
			top: 50%;
			right: 4px;
			margin-top: 1px;

			-webkit-transition: inherit;
					-o-transition: inherit;
					transition: inherit;
		}

		.navigation li:hover > a,
		.navigation .active > a,
		.navigation .current > a{
			color: #0097ad;
		}

		.navigation a::before{
			display: none !important;
		}

		.navigation li:hover > a::after,
		.navigation .active > a::after,
		.navigation .has_submenu.current > a::after{
			color: #0097ad;

			-webkit-transition: inherit;
					-o-transition: inherit;
					transition: inherit;
		}

		.navigation .active > a::after{
			content: "\e87b";
		}

		.vm_wrap .submenu,
		.md_nocsstransitions .submenu{
			display: none;
		}

		/* --------------------------------------------
			3.2.1 Vertical
		-------------------------------------------- */

			.vm_wrap{
				position: fixed;
				top: 0;
				right: 0;
				height: 100%;
				background: #181818;
				padding: 100px 25px 25px 30px;
				width: 300px;
				z-index: 101;
				-webkit-backface-visibility: hidden;

				-webkit-transition: -webkit-transform .4s ease;
						transition: 		-webkit-transform .4s ease;
						-o-transition: 		transform .4s ease;
						transition: 		transform .4s ease;
						transition: 		transform .4s ease, -webkit-transform .4s ease;

				-webkit-transform: translateX(100%);
					-ms-transform: translateX(100%);
						transform: translateX(100%);
			}

			.vm_wrap.opened{
				-webkit-transform: translateX(0%);
					-ms-transform: translateX(0%);
						transform: translateX(0%);
			}

			.vm_wrap.hf{
				height: 101%;
			}

			.vm_wrap::before{
				content: "";
				display: block;
				position: absolute;
				top: 0;
				right: 100%;
				width: 10000%;
				height: 100%;
				background: rgba(24, 24, 24, .6);
				opacity: 0;
				visibility: hidden;

				-webkit-transition: opacity .4s ease, visibility .4s ease;
						-o-transition: opacity .4s ease, visibility .4s ease;
						transition: opacity .4s ease, visibility .4s ease;
			}

			.vm_wrap.opened::before{
				opacity: 1;
				visibility: visible;
			}

			.vm_wrap .navigation li:not(:last-child){
				margin-bottom: 12px;
			}

			.vm_wrap .has_submenu > .submenu{
				padding-left: 10px;
			}

			.vm_wrap .submenu > li{
				margin: 6px 0 0 !important;
			}

		/* --------------------------------------------
			3.2.2 Horizontal
		-------------------------------------------- */

			.hm_wrap{
				display: inline-block;
				vertical-align: middle;
				margin: 7px 0 0 80px;
				padding-right: 250px;
				opacity: 0;
				visibility: hidden;
				/*position: absolute;*/
				top: 0px;

				-webkit-transition: -webkit-transform .4s ease, opacity .4s ease, visibility .4s ease;
						-webkit-transition: 		opacity .4s ease, visibility .4s ease, -webkit-transform .4s ease;
						transition: 		opacity .4s ease, visibility .4s ease, -webkit-transform .4s ease;
						-o-transition: 		transform .4s ease, opacity .4s ease, visibility .4s ease;
						transition: 		transform .4s ease, opacity .4s ease, visibility .4s ease;
						transition: 		transform .4s ease, opacity .4s ease, visibility .4s ease, -webkit-transform .4s ease;

				-webkit-transform: translateY(-80%);
					-ms-transform: translateY(-80%);
						transform: translateY(-80%);
			}

			.hm_wrap.opened{
				opacity: 1;
				visibility: visible;

				-webkit-transform: translateY(0%);
					-ms-transform: translateY(0%);
						transform: translateY(0%);
			}

			.hm_wrap .navigation::after{
				content: "";
				display: block;
				clear: both;
			}

			.hm_wrap .navigation > li{
				float: left;
			}

			.hm_wrap .navigation > li:not(:last-child){
				margin-right: 25px;
			}

			.hm_wrap .submenu{
				font-size: 14px;
				white-space: nowrap;
				padding: 23px 30px;
				position: absolute;
				top: 100%;
				left: -30px;
				margin-top: 22px;
				background: #181818;
				opacity: 0;
				visibility: hidden;

				-webkit-animation: zoomOutUp .7s ease;
						animation: zoomOutUp .7s ease;

				-webkit-transition: opacity .7s ease, visibility .7s ease .7s;
						-o-transition: opacity .7s ease, visibility .7s ease .7s;
						transition: opacity .7s ease, visibility .7s ease .7s;
			}

			.hm_wrap .active > .submenu,
			.md_no-touch .hm_wrap .has_submenu:hover > .submenu{
				opacity: 1;
				visibility: visible;

				-webkit-animation: zoomInDown .7s ease;
						animation: zoomInDown .7s ease;

				-webkit-transition: opacity .7s ease, visibility .7s ease;
						-o-transition: opacity .7s ease, visibility .7s ease;
						transition: opacity .7s ease, visibility .7s ease;	
			}

			.hm_wrap .submenu .submenu{
				top: -23px;
				left: 100%;
				margin: 0 0 0 20px;

				-webkit-animation-name: zoomOutLeft;
						animation-name: zoomOutLeft;
			}

			.hm_wrap .submenu .active > .submenu,
			.md_no-touch .hm_wrap .submenu .has_submenu:hover > .submenu{
				-webkit-animation-name: zoomInLeft;
						animation-name: zoomInLeft;
			}

			.md_no-touch .hm_wrap .navigation > .has_submenu > .submenu::after,
			.md_no-touch .hm_wrap .submenu .has_submenu > .submenu::after{
				content:"";
				display: block;
				position: absolute;
				width: 100%;
				bottom: 100%;
				height: 22px;
				left: 0;
			}

			.md_no-touch .hm_wrap .submenu .has_submenu > .submenu::after{
				top: 0;
				bottom: auto;
				left: auto;
				right: 100%;
				width: 20px;
				height: 100%;
			}

			.hm_wrap .submenu > li:not(:last-child){
				margin-bottom: 6px;
			}

			.hm_wrap .submenu .has_submenu > a::after{
				content: "\e87a";
			}

			.hm_wrap .submenu .has_submenu.current > a::after,
			.hm_wrap .submenu .active > a::after{
				content: "\e879";
			}

