		}

		.page_actions{
			text-align: right;
			position: relative;
			z-index: 1;
		}

		.page_actions::after{
			content: "";
			display: block;
			clear: both;
		}

		.page_actions > li{
			display: inline-block;
			vertical-align: middle;
		}

