		textarea,
		.qty_btn,
		input:not([type="submit"]){
			color: #505050;
			font-size: 14px;
			height: 40px;
			padding: 5px 10px;
			width: 100%;
			background: #f8f8f8;
			border: 1px solid #ddd;
		}

		textarea{
			padding-top: 10px;
			padding-bottom: 10px;
			height: auto;
			resize: none;
		}

		label{
			display: inline-block;
			margin-bottom:7px;
		}

		input[type="radio"],
		input[type="checkbox"]{
			display: none !important;
		}

		input[type="radio"] + label,
		input[type="checkbox"] + label{
			position: relative;
			cursor: pointer;
			padding-left: 30px;
			margin-bottom: 0px;
		}

		input[type="radio"] + label::before,
		input[type="checkbox"] + label::before{
			content: "";
			font-family: 'fontello';
			text-align: center;
			color: #505050;
			display: block;
			background-color: #f8f8f8;
			border: 1px solid #ddd;
			position: absolute;
			top: 50%;
			left: 0;
			margin-top: -11px;
			width: 22px;
			height: 22px;
			line-height: 20px;
		}

		input[type="radio"] + label::before{
			border-radius: 50%;
			font-size: 12px;
		}

		input[type="checkbox"]:checked + label::before{
			content: "\e813";
		}

		input[type="radio"]:checked + label::before{
			content: "\e932";
		}

		form [class|="col"] > input[type="radio"] + label,
		form [class|="col"] > input[type="checkbox"] + label{
			padding-top: 4px;
			padding-bottom: 4px;
		}

		.nav_searchform{
			width: 0;
			position: relative;
			z-index: 102;
			top: -1px;
			opacity: 0;
			visibility: hidden;

			-webkit-transition: opacity .2s ease, visibility .2s ease;
					-o-transition: opacity .2s ease, visibility .2s ease;
					transition: opacity .2s ease, visibility .2s ease;
		}

		.menu_opened .nav_searchform{
			opacity: 1;
			visibility: visible;

			-webkit-transition: opacity .4s ease .3s, visibility .4s ease .3s;
					-o-transition: opacity .4s ease .3s, visibility .4s ease .3s;
					transition: opacity .4s ease .3s, visibility .4s ease .3s;
		}

		.nav_searchform::after{
			content: "";
			display: block;
			clear: both;
		}

		.nav_searchform input[type="search"]{
