		}

		.ts_nav > li > a,
		.tabs_nav > li > a{
			color: #888;
			font-weight: 700;
			cursor: pointer;
			display: block;
			border: 2px solid #ddd;
			min-width: 125px;
			padding: 6px 20px;
			background: #fff;
			overflow: hidden;
			position: relative;
			z-index: 1;
			text-decoration: none !important;

			-webkit-transition: color .5s ease, border-color .5s ease, background-color .5s ease;
					-o-transition: color .5s ease, border-color .5s ease, background-color .5s ease;
					transition: color .5s ease, border-color .5s ease, background-color .5s ease;
		}


		.ts_nav > li > a::after,
		.tabs_nav > li > a::after{
			content: "";
			position: absolute;
			top: -2px;
			left: 0;
			width: 100%;
			height: 108%;
			z-index: -1;
			opacity: 0;
			background-color: #181818;

			-webkit-transform: translateY(70%);
					-ms-transform: translateY(70%);
			    transform: translateY(70%);

			-webkit-transition: -webkit-transform .5s ease, opacity .5s ease;
					-webkit-transition: 		opacity .5s ease, -webkit-transform .5s ease;
					transition: 		opacity .5s ease, -webkit-transform .5s ease;
					-o-transition: 		transform .5s ease, opacity .5s ease;
					transition: 		transform .5s ease, opacity .5s ease;
					transition: 		transform .5s ease, opacity .5s ease, -webkit-transform .5s ease;
		}

		.tabs_nav > .active > a::after,
		.ts_nav > .active > a::after{
			opacity: 1;

			-webkit-transform: translateY(0px);
					-ms-transform: translateY(0px);
			    transform: translateY(0px);
		}

		.ts_nav > li > a::before,
		.tabs_nav > li > a::before{
			display: none !important;
		}

		.tab{
			font-size: 14px;
