/*jtab min css**/
/*!
 * JTab is “Displays content in tabs for a framework JQuery”.
 *
 * @author demorfi <demorfi@gmail.com>
 * @version 1.0
 * @source https://github.com/demorfi/jtab
 * @license http://opensource.org/licenses/MIT Licensed under MIT License
 */.jtab-ul{font-family:Arial,Helvetica;font-size:small;list-style-type:none;margin:0;overflow:hidden;padding:0}
 .jtab-ul li{float:left;}
 .jtab-ul li a{border-bottom:25px solid rgba(0,0,0,0.15);border-right:25px solid transparent;color:#fff;float:left;height:0;line-height:28px;padding:0 35px;
  position:relative;text-decoration:none;text-transform:uppercase;z-index:1;background:transparent;-o-transition:border-bottom-color .4s ease;
  -webkit-transition:border-bottom-color .4s ease;transition:border-bottom-color .4s ease}
 .jtab-ul li a:hover,.jtab-ul li a:focus{border-bottom-color:rgba(0,137,191,0.6);outline:0}
 .jtab-ul li a.jtab-selected{z-index:2;border-bottom-color:rgba(1,1,1,0.75)}
 .jtab-content-list{
  font-family:Arial,Helvetica;font-size:small;
  background-color:#fff;border-top:2px solid rgba(1,1,1,0.75);
  padding:20px 0;}
 .jtab-content-list>div,.jtab-content-list .jtab-content{display:none;position:relative;
  z-index:2;min-height:210px}
 .jtab-content-list>div:target,.jtab-content-list>div:last-of-type,.jtab-content-list .jtab-content:target,.jtab-content-list .jtab-content:last-of-type{display:block}
 .jtab-content-list>div:target ~ div:last-of-type,.jtab-content-list .jtab-content:target ~ div:last-of-type{display:none}
 .jtab-content-list>div.jtab-loading,.jtab-content-list .jtab-content.jtab-loading{background:url('') no-repeat 50% 50%}
.jtab-wrapper li{
  margin-bottom: 0px !important;
}

 /*end of  jtab min css*/

