    -------News Slider------------------------------------*/
.header .top-bar {
    background: #EFEFEF !important;
}
.dropdown-Ssubmenu {
    position:relative;
}
.dropdown-Ssubmenu>.dropdown-menu {
    top:0;
    left:100%;
    margin-top:-6px;
    margin-left:-1px;
    border-radius:0 6px 6px 6px;
}
.dropdown-Ssubmenu:hover>.dropdown-menu {
    display:block;
}
.dropdown-Ssubmenu>a:after {
    display:block;
    content:" ";
    float:right;
    width:0;
    height:0;
    border-color:transparent;
    border-style:solid;
    border-width:5px 0 5px 5px;
    border-left-color:#cccccc;
    margin-top:5px;
    margin-right:-10px;
}
.dropdown-Ssubmenu:hover>a:after {
    border-left-color:#ffffff;
}
.dropdown-Ssubmenu.pull-left {
    float:none;
}
.dropdown-Ssubmenu.pull-left>.dropdown-menu {
    left:-100%;
    margin-left:10px;
    border-radius:6px 0 6px 6px;
}


